#login-container
{
          border: 2px solid black;
          margin: auto;
         
          height: 100vh;
          width: 100vw;
          background-color: #bbd8c0;

}
#login-container-box
{
          background-color:#fbfcfb;
          height: 630px;
          width: 1050px;
          display: grid;
          padding: 10px;
          grid-template-columns: 40% 60%;
          border-radius:25px;
}
h1{
          font-weight: bolder;
}
#login-box
{
          padding: 30px;
}
#login
{
         
         padding-top: 30px;
         padding-bottom: 30px;
       
}
#button
{
          color: white;
          text-decoration: none;
        
          background-color: black;
          color: white;
          width: 80px;
          padding: 10px;
          margin-top: 20px;
          box-shadow: 10rem black;
          
}

#form
{
          border: none;
          box-shadow: none;
          margin: 0px;
          padding: 0px;
          
}
