/* App.css */
.container {
          max-width: 400px;
          margin: 0 auto;
          padding: 20px;
          background-color: #b4d9e8;
          border-radius: 8px;
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        }
        
        .register-heading {
          text-align: center;
          padding-top: 10px;
          color: #333;
        }
        
        .form-group {
          margin-bottom: 20px;
        }
        
        .form-control {
          width: 100%;
          padding: 10px;
          font-size: 16px;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
        
        .error {
          color: red;
        }
        
        .btn {
          display: inline-block;
          padding: 10px 20px;
          font-size: 16px;
          text-align: center;
          text-decoration: none;
          cursor: pointer;
          border: none;
          border-radius: 4px;
          background-color: #007bff;
          color: #fff;
        }
        
        .btn-primary {
          background-color: #007bff;
        }
        
        .text-center {
          text-align: center;
        }
        
        /* Add a background image to the container */
        .container {
        
          background-position: center;
          color: #111111; /* Set text color to white for better visibility on the background */
        }
        
        /* Add some spacing to the link */
        .text-center a {
          margin-left: 5px;
        }
        #register-container
        {
          background-image: url('./images/hm5.avif');
          background-size: cover;
          background-blend-mode:overlay;
        }
        #register-container.img
        {
          opacity: 50%;
        }